<script>
import { Icon,Button,Notify } from 'vant';
// import axios from 'axios'
export default {
    name:'login',
    data() {
        return {
            phone:'',
            zhengyam:'',
            yzm:'',
            mima:'',
        }
    },
    components:{
        [Icon.name]:Icon,
        [Button.name]:Button,
        [Notify.name]:Notify 
    },
    methods:{
        getyzm(){
            // console.log(this.phone.length);
            if(this.phone.length===11){
                this.$axios({
                    method:'post',
                    url:' https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms',
                    data:{
                            "mobile":this.phone,
                            "type":'register'
                        }    
                    }).then(res=>{
                        this.zhengyam=res.data.data
                        
                        if (res.data.msg==='账号已经存在') {
                            Notify({ type: 'success', duration: 6000,message:res.data.msg+'请登录'})
                        }else{
                            Notify({ type: 'success', duration: 6000,message: '验证码' + this.zhengyam })
                        }
                        console.log("222",res);
                })
            }else{
                Notify('请输入正确的手机号')
            }
        },
        //注测
        getreg(){
            // console.log(this.phone);
            if(this.yzm===this.zhengyam){
                this.$axios({
                    method:'post',
                    url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/reg',
                    data:{
                            "username":this.phone,
                            "password":this.mima,
                            "vercode":this.zhengyam
                        }    
                    }).then(res=>{
                       
                        Notify({ type: 'success', duration: 6000,message:res.data.msg})
                        // console.log("333",res); 
                        
                        // 跳转登录
                        if(res.data.msg=='注册成功'){
                            this.$router.push({
                                name:'login'
                                // params:{
                                //     token:token
                                // }
                            })
                }
               
                        return this.info
                        
                })
                // console.log(this.info);
               
                
            }else{
                Notify('请输入正确的验证码')
            }
               
                
        }

    },
    created(){
       
    }
}
</script>

