import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '@/views/Login/login.vue'
import home from '@/views/home.vue'
import register from '@/views/Login/register.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/register',
    name: 'register',
    component: register
  },
  {
    path: '/home',
    name: 'home',
    component: home,
    meta:{
      root:true
    }
  },{
    path: '/my',
    name: 'my',
    component: ()=>import('@/views/user/my.vue'),
    meta:{
      root:true
    }
  },{
    path: '/public',
    name: 'public',
    component: ()=>import('@/views/public/public.vue'),
    meta:{
      root:true
    }
  },{
    path: '/sousuo',
    name: 'sousuo',
    component: ()=>import('@/views/sousuo/search.vue')
  },{
    path: '/mymsg',
    name: 'mymsg',
    component: ()=>import('@/views/user/mymsg.vue')
  },{
    path: '/listcontent',
    name: 'listcontent',
    component: ()=>import('@/views/listContent/index.vue')
  },
  {
    path: '/mypublic',
    name: 'mypublic',
    component: ()=>import('@/views/user/myPublic.vue')
  },
  {
    path: '/mychangemima',
    name: 'mychangemima',
    component: ()=>import('@/views/user/myChangeMima.vue')
  },
  {
    path: '/myshoucang',
    name: 'myshoucang',
    component: ()=>import('@/views/user/myShouCang.vue')
  },

  // 
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})
  // router.beforeEach((to,from,next)=>{
  //     // console.log(to);
  //     // console.log(from);
  //   next()
  // })
export default router
